/* export fonts */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&family=Sora:wght@100;200;300;400;500;600;700;800&display=swap");
/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Sora', sans-serif; */

:root {
  --font-xxl-h1: 3.75rem;
  --font-xl-h2: 2.25rem;
  --font-l-h3: 1.375rem;
  --font-pera-p: 1.125rem;
  --font-small-p: 1rem;
  --font-extra-small-a-span: 0.9375rem;
  --box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.08),
    0px 2px 10px rgba(0, 0, 0, 0.08), 0px 16px 20px rgba(0, 0, 0, 0.16);
  --dark-violet-color: #562af0;
  --light-violet-color: #4d3add;
  --dark-sand-color: #464646;
  --dark-blue-color: #0a0535;
  --section-bg-color: #f3f0ff;
  --dark-color: #000000;
  --light-color: #ffffff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-size: 1rem;
  overflow-x: hidden;
  font-family: "Poppins", sans-serif;
}

img {
  vertical-align: middle;
}

input,
a,
button {
  border: none;
  outline: none;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  transition: all 0.5s ease;
  font-size: var(--font-extra-small-a-span);
  color: var(--dark-color);
  text-transform: capitalize;
}

::after,
::before {
  box-sizing: border-box;
  transition: all 0.5s ease;
}

ul {
  list-style: none;
}

/* utilities classes */

.container {
  max-width: 1300px;
  margin: 0 auto;
  position: relative;
}

.row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.section {
  max-height: 300vh;
  min-height: 100vh;
  height: fit-content;
  overflow: hidden;
  position: relative;
}

.img-box {
  overflow: hidden;
  vertical-align: middle;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.nav-btn {
  font-family: "Sora", sans-serif;
}

.title {
  font-size: var(--font-xxl-h1);
  font-weight: 600;
  pointer-events: none;
  line-height: 1.4;
}

.sub-title {
  font-size: var(--font-xl-h2);
  font-weight: 600;
  width: 100%;
  pointer-events: none;
  text-align: center;
}

.paragraph {
  font-size: var(--font-pera-p);
  font-weight: 400;
  line-height: 1.5;
  pointer-events: none;
}

.main-btn {
  font-size: var(--font-extra-small-a-span);
  font-weight: 400;
  color: var(--light-color);
  background-color: var(--dark-violet-color);
  padding: 0.7rem 3rem;
  box-shadow: 6px 6px 0px violet;
  border-radius: 5px;
}

.main-btn:hover {
  box-shadow: none;
}

.rocket {
  position: absolute;
}

.wrapper {
  min-height: 100vh;
  height: fit-content;
}

/* navbar big screen */

nav {
  padding: 2rem 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  transition: all 0.5s ease;
  z-index: 100;
  background-color: var(--light-color);
}

nav.active {
  box-shadow: 0 0 20px #dddddd;
  transition: all 0.5s ease;
  padding: 1rem;
}

nav nav .container,
nav .row {
  width: 100%;
}

nav .nav-btns {
  width: 15rem;
}

nav .nav-btns a {
  padding: 0.4rem 0;
}

nav .nav-btns a::after {
  position: absolute;
  z-index: 2;
  width: 0;
  height: 2px;
  background-color: var(--dark-violet-color);
  content: "";
  bottom: 0;
  left: 0;
}

nav .nav-btns a:hover {
  color: var(--dark-violet-color);
}

nav .nav-btns a:hover::after {
  width: 100%;
}

nav .btn-2 {
  color: var(--dark-violet-color);
  border: 1px solid var(--dark-violet-color);
  padding: 0.6rem 2rem;
  border-radius: 5px;
}

nav .btn-2:hover {
  background-color: var(--dark-violet-color);
  color: var(--light-color);
}

/* banner */

.banner {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding: 2rem 0;
}

.banner .container,
.banner .row {
  width: 100%;
  height: 100%;
  min-height: 75vh;
  background-image: linear-gradient(
    to right bottom,
    #fff2db,
    #ffebe0,
    #ffe7eb,
    #fde6f6,
    #ebe8fd
  );
  border-radius: 20px;
  box-shadow: 0 0 10px #f7f7f7;
}

.banner .banner-content {
  flex: 0 0 50%;
  max-width: 50%;
  padding: 4rem 0 4rem 3rem;
}

.banner .banner-content p {
  margin: 1rem 0 2rem 0;
}

.banner .banner-pic {
  flex: 0 0 50%;
  max-width: 50%;
  overflow: hidden;
}

.banner .banner-pic img {
  width: 100%;
  height: 100%;
}

.banner .rocket-container-1 {
  top: -10%;
  left: -5%;
  z-index: 2;
}

/* about section */

.about {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  background-color: var(--section-bg-color);
}

.about .container {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* padding: 2rem 0 4rem 0; */
}

.about .container .row {
  width: 100%;
  align-items: flex-start;
  margin-top: 4rem;
}

.about .about-content {
  flex: 0 0 50%;
  max-width: 50%;
  padding: 5rem 0 5rem 3rem;
}

.about .about-content p {
  font-weight: 500;
  width: 80%;
}

.about .about-content p:nth-of-type(2) {
  margin-top: 1rem;
}

.about .about-pic {
  max-width: 50%;
  flex: 0 0 50%;
  height: 28rem;
  position: relative;
}

.about .about-pic .img-box {
  box-shadow: var(--box-shadow);
  border-radius: 20px;
  background-color: var(--light-color);
}

.about .about-pic .about-img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  height: 24rem;
  border-radius: 20px;
  z-index: 2;
  background-color: var(--section-bg-color);
}

.about .about-pic .about-dog {
  top: -32%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 11;
  width: 14rem;
}

.about .rocket-2 {
  top: 0%;
  left: 0;
  z-index: 9;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about .shape-1 {
  bottom: 10%;
  left: -5%;
}

.about .shape-2 {
  top: 10%;
  right: -5%;
}

.about .shape-3 {
  top: 10%;
  left: -7%;
}
